import React from 'react';
import PropTypes from 'prop-types';
import styles from './styles/TrTooltip.module.scss';

export const TrTooltip = ({ label, position, tooltipText, ...props }) => {
    return (
        <>
            <div className={`${styles['tooltip-wrap']} ${styles[position]}`}>
                <span className={styles['tooltip-label']}>{label}</span>
                <p className={styles['tooltip-text']}>{tooltipText}</p>
            </div>
        </>
    );
};

TrTooltip.propTypes = {
    label: PropTypes.string,
    position: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
    tooltipText: PropTypes.string,
};

TrTooltip.defaultProps = {
    label: 'Tooltip',
    position: 'top',
    tooltipText: '툴팁 도움말입니다.'
};
